import { Component, OnInit, ViewChild } from '@angular/core';
import { routerTransition } from '../../router.animations';
import {MatPaginator, MatSort, MatTableDataSource} from '@angular/material';
import { FormControl, FormBuilder, FormGroup, Validators} from '@angular/forms';
import { SelectionModel } from '@angular/cdk/collections';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { ToasterService} from 'angular5-toaster';


@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.scss'],
    animations: [routerTransition()]
})
export class DashboardComponent  {
  displayedColumns = ['select', 'block', 'flat_no', 'floor', 'owner_name',  'owner_contact','flat_status', 'actions'];
  societyadmin = JSON.parse(localStorage['societyadmin']);
  
  manager;
  managerexists: boolean = false;

  chairman;
  chairmanexists: boolean = false;

  flatlistcount;
  response;
  securitylistcount;
  visitorlistcount
  dataSource: MatTableDataSource<FlatDetails>;
  selection = new SelectionModel<FlatDetails>(true, []);

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  flatdetails: FlatDetails[];
  private toasterService: ToasterService;

  constructor(
    private http: HttpClient,
    public router: Router,
    toasterService: ToasterService,
    private _formBuilder: FormBuilder
  ) {
    this.toasterService = toasterService;

        if (localStorage.getItem('manager')) {

            this.manager = JSON.parse(localStorage['manager']);
            this.managerexists = true;

        }
        console.log('ManagerExists:' + this.managerexists);

        if (localStorage.getItem('chairman')) {

          this.chairman = JSON.parse(localStorage['chairman']);
          this.chairmanexists = true;

      }
      console.log('ChairmanExists:' + this.chairmanexists);


    this.getFlatownerData();
    this.getListOfSecurity();
    this.getListOfVisitor();

   }
   
   getListOfSecurity(){
    this.http.get<Securitylist[]>('api/securitylistdetails/' + this.societyadmin.id).subscribe(data=> {
      this.securitylistcount = data.length;
      console.log(this.securitylistcount);

   });
   }
   getListOfVisitor(){
    this.http.get<Securitylist[]>('api/visitorlistdetails/' + this.societyadmin.id).subscribe(data=> {
      this.visitorlistcount = data.length;
      console.log(this.visitorlistcount);

   });
   }

   getFlatownerData(){

    this.http.get<FlatDetails[]>('api/flatownerlistdetails/' + this.societyadmin.id).subscribe(data=> {
      this.flatdetails = data;  
      this.flatlistcount= this.flatdetails.length;
      console.log(this.flatdetails);
      this.dataSource = new MatTableDataSource(this.flatdetails);
     this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    });
  
  }

  ngOnInit() {
    
  }
  changeStatus(flat,id){

    flat=! flat;
     const flat_details ={
      status: flat
     };
     console.log(flat_details);
     console.log(id);

     
    this.http.put<FlatDetails[]>('api/updateflatstatus/' + id , flat_details).subscribe(data=> {
      var response:any = data;
      if(response.success == true){
        this.toasterService.pop('success', response.message);
     
      }
      else if(response.success == false){
        this.toasterService.pop('warning', response.message);  
     
      }
         this.getFlatownerData();

    });


  }

applyFilter(filterValue: string) {
  filterValue = filterValue.trim(); // Remove whitespace
  filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
  this.dataSource.filter = filterValue;
}
/** Whether the number of selected elements matches the total number of rows. */
isAllSelected() {
  const numSelected = this.selection.selected.length;
  const numRows = this.dataSource.data.length;
  return numSelected == numRows;
}

/** Selects all rows if they are not all selected; otherwise clear selection. */
masterToggle() {
  this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
}

getFlatOwnerId(flatowner){
  console.log(flatowner);

  localStorage.setItem('flatowner', JSON.stringify(flatowner));

  this.router.navigate(['/flatowner_details']);


}

}
export interface FlatDetails{
 
}

export interface Securitylist{
 
}




